legend.onAdd = function(map) {
  var div = L.DomUtil.create("div", "legend");
  div.innerHTML += "<h4>Totalcounts (TC)</h4>";

  div.innerHTML += getColorBoxHTML('#8E44AD', '0 - 80');
  div.innerHTML += getColorBoxHTML('#F39C12', '80 - 120');
  div.innerHTML += getColorBoxHTML('#D35400', '120 - 200');
  div.innerHTML += getColorBoxHTML('#27AE60', '200 - 350');
  div.innerHTML += getColorBoxHTML('#3498DB', '350 - 500');
  div.innerHTML += getColorBoxHTML('#E74C3C', '>500');

  return div;
};

// Helper function to create HTML for color box
function getColorBoxHTML(color, label) {
  return '<i style="background: ' + color + '"></i><span>' + label + '</span><br>';
}

const provider = new OpenStreetMapProvider();

